* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: sans-serif;
}

ul {
  list-style: none;
  padding: 0px;
}

a {
  text-decoration: none;
}

#nav-bar {
  position: sticky;
  top: 0px;
  z-index: 10;
}
@import 'scroll';

.navbar {
  background-image: linear-gradient(to right, #a517ba, #5f1782);
  padding: 0px !important;
  &-brand {
    img {
      height: 40px;
      padding-left: 20px;
    }
  }
  &-toggler {
    outline: 0 !important;
    box-shadow: none !important;
    border: none !important;
    .fa-bars {
      color: white;
      font-size: 40px;
    }
  }

  &-nav {
    li {
      padding: 0 10px;
      a {
        color: #fff !important;
        font-weight: 600;
        float: right;
        text-align: left;
        width: 120px;
      }
    }
  }
}
.promo {
  background: linear-gradient(to right, #a517ba, #5f1782);
  color: white;
  padding-top: 5px;
  &-title {
    font-size: 40px;
    font-weight: 600;
    margin-top: 100px;
  }
  a {
    color: white;
  }
  .play-btn {
    width: 45px;
    margin: 20px;
  }
  .bottom-img {
    width: 100%;
  }
}
.service {
  padding: 80px 0px;
  &-title {
    position: relative;
    &::before {
      content: '';
      background: #7b1798;
      height: 5px;
      width: 200px;
      margin-left: auto;
      margin-right: auto;
      display: block;
      // transform: translateY(63px);
      position: absolute;
      left: 0;
      right: 0;
      bottom: -6px;
    }
    &::after {
      content: '';
      background: #7b1798;
      height: 10px;
      width: 50px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 40px;
      display: block;
      transform: translateY(8px);
    }
  }
  &-item {
    padding: 20px;
    h4 {
      padding: 5px;
      margin-top: 20px;
    }
  }
  &-img {
    width: 100px;
    margin-top: 20px;
  }
  .btn-primary {
    box-shadow: none;
    padding: 8px 25px;
    border: none;
    border-radius: 20px;
    background-image: linear-gradient(to right, #a517ba, #5f1782);
  }
}
.about-us {
  background: #f8f9fa;
  padding: 50px 0px;
  position: relative;
  &__title {
    position: relative;
    &::before {
      content: '';
      background: #7b1798;
      height: 5px;
      width: 200px;
      margin-left: auto;
      margin-right: auto;
      display: block;
      // transform: translateY(63px);
      position: absolute;
      left: 0;
      right: 0;
      bottom: -6px;
    }
    &::after {
      content: '';
      background: #7b1798;
      height: 10px;
      width: 50px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 40px;
      display: block;
      transform: translateY(8px);
    }
  }
  &__content {
    ul {
      li {
        margin: 10px 0px;
      }
    }
  }
  &__description {
    font-size: 40px;
    font-weight: 600;
    margin-top: 8%;
  }
}
.testimonials {
  margin: 100px 0px;
  &__title {
    position: relative;
    &::before {
      content: '';
      background: #7b1798;
      height: 5px;
      width: 200px;
      margin-left: auto;
      margin-right: auto;
      display: block;
      // transform: translateY(63px);
      position: absolute;
      left: 0;
      right: 0;
      bottom: -6px;
    }
    &::after {
      content: '';
      background: #7b1798;
      height: 10px;
      width: 50px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 40px;
      display: block;
      transform: translateY(8px);
    }
  }
  &__description {
    border-left: 4px solid #7b1798;
    margin: 50px 0px;
    img {
      height: 60px;
      width: 60px;
      border-radius: 50%;
      margin: 0 10px;
    }
  }
  &__about-autor {
    display: inline-block;
    font-size: 12px;
  }
}

.social-media {
  background: #f8f9fa;
  padding: 100px 0px;
  &__title {
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 30px;
  }
  &__icons {
    a {
      img {
        width: 60px;
        transition: 0.5s;
        &:hover {
          transform: translateY(-10px);
        }
      }
    }
  }
}

.footer {
  background-image: linear-gradient(to right, #a517ba, #5f1782);
  color: white;
  &__img {
    width: 100%;
  }
  &__box {
    padding: 20px;
    img {
      width: 120px;
      margin-bottom: 20px;
    }
    p {
      .fa {
        margin-right: 8px;
        font-size: 25px;
        height: 40px;
        width: 40px;
        text-align: center;
        padding-top: 7px;
        border-radius: 2px;
        background-image: linear-gradient(to right, #a517ba, #5f1782);
      }
    }
    .btn-primary {
      box-shadow: none !important;
      border: none;
      border-radius: 0;
      margin-top: 30px;
      padding: 10px 28px;
      background: linear-gradient(to right, #a517ba, #5f1782);
    }
  }
  .form-control {
    box-shadow: none !important;
    border: none;
    border-radius: 0;
    margin-top: 25px;
    max-width: 250px;
  }
  hr {
    background-color: white;
  }
  .copyright {
    margin-bottom: 0px;
    padding-bottom: 20px;
  }
}
